<script>
	import request from "@/assets/js/request"
	import Gallery from "@/components/Gallery"

	export default {
		name: "Business",
		data () {
			return {
				names: "detail3",
				title: "",
				modalW: "",
				modalH: "",
				modalHH: "",
				data: "",
				galleryShow: false,
				img: []
			}
		},
		mounted: function () {
			let that = this
			let names = that.$route.name || "detail3"
			that.names = names
			that.setTitle(names)
			that.$nextTick(() => {
				that.modalW = "width:" + ($(window).width() - 60) / 100 + "rem"
				that.modalH = "height:" + ($(window).height() - 280) / 100 + "rem; overflow-y:auto;"
				that.modalHH = "height:" + ($(window).height() - 280 - 150) / 100 + "rem; overflow-y:auto;"
				$(window).on("resize load", function () {
					that.modalW = "width:" + ($(window).width() - 60) / 100 + "rem"
					that.modalH = "height:" + ($(window).height() - 280) / 100 + "rem; overflow-y:auto;"
					that.modalHH = "height:" + ($(window).height() - 280 - 150) / 100 + "rem; overflow-y:auto;"
				})
				$(document).on("click", ".content img", function () {
					that.headleGalleryClick()
				})
			})
		},
		methods: {
			getInfo (names) {
				let self = this
				let arr = []
				request.getRequest("GetBusinessinfo", {name: names}).then(res => {
					self.data = res.Data
					setTimeout(function () {
						$(".content img").each(function () {
							arr.push($(this).attr("src"))
						})
						self.img = arr
					}, 1000)
				})
			},
			setTitle (names) {
				switch (names) {
					case "detail1":
						this.title = ""
						break
					case "detail2":
						this.title = ""
						break
					case "detail3":
						this.title = ""
						break
					case "detail4":
						this.title = ""
						break
					case "detail5":
						this.title = ""
						break
				}
				if (this.title === "") {
					this.getInfo("")
				} else {
					this.getInfo(this.title)
				}

			},
			headleGalleryClick () {
				this.galleryShow = true
			},
			headleGalleryClose () {
				this.galleryShow = false
			}
		},
		components: {
			Gallery
		},
		watch: {
			$route (to, from) {
				let names = to.name
				this.names = names
				this.setTitle(names)
			}
		}
	}
</script>

